import { useState } from "react";
import { IoMdStar, IoMdStarOutline } from "react-icons/io";

const StarRating = ({
  label,
  name,
  value,
  onChange = () => {},
  size = 16,
  align = "left",
  maxRating = 5,
  containerClassName = "gap-2",
}) => {
  const [rating, setRating] = useState(value);
  const [hover, setHover] = useState(null);

  const handleRatingChange = (newRating) => {
    setRating(newRating);
    onChange({ target: { name, value: newRating } });
  };

  return (
    <div className={`flex items-center ${containerClassName}`}>
      {label && align === "left" && (
        <label className="capitalize text-sm font-medium font-secondary">
          {label}
        </label>
      )}
      <div className="STARS flex">
        {[...Array(maxRating)].map((_, index) => (
          <span
            key={index}
            className="STAR cursor-pointer"
            onMouseEnter={() => setHover(index + 1)}
            onMouseLeave={() => setHover(null)}
            onClick={() => handleRatingChange(index + 1)}
          >
            {index + 1 <= (hover || rating) ? (
              <IoMdStar size={size} className="text-yellow-500" />
            ) : (
              <IoMdStarOutline size={size} className="text-gray-300" />
            )}
          </span>
        ))}
      </div>
      {label && align === "right" && (
        <label className="capitalize text-sm font-medium font-secondary">
          {label}
        </label>
      )}
    </div>
  );
};

export default StarRating;
