import {
  keepPreviousData,
  useMutation,
  useQuery,
  useQueryClient,
} from "@tanstack/react-query";
import {
  createReview,
  deleteReview,
  getAllFeaturedVariants,
  getAllNewArrivals,
  getAllReviews,
  getAllTopSellingProducts,
  getAllUtsavVariants,
  getAllVariants,
  getFeaturedVariantsBasedOnCategory,
  getProductById,
  getRatingDetails,
  getRecommendation,
  getReviewById,
  getVariant,
  updateReview,
} from "../api/productApis";

const useProductById = (productId) => {
  return useQuery({
    queryKey: ["productGroup", productId],
    queryFn: () => getProductById(productId),
    staleTime: 1000 * 60 * 5,
    enabled: !!productId,
  });
};

const useVariants = (params) => {
  return useQuery({
    queryKey: ["variants", params],
    queryFn: () => getAllVariants(params),
    staleTime: 1000 * 60 * 5,
    placeholderData: keepPreviousData,
  });
};

const useAllUtsavVariants = () => {
  return useQuery({
    queryKey: ["variants", "utsav"],
    queryFn: getAllUtsavVariants,
    staleTime: 1000 * 60 * 5,
  });
};

const useAllFeaturedVariants = () => {
  return useQuery({
    queryKey: ["variants", "featured"],
    queryFn: getAllFeaturedVariants,
    staleTime: 1000 * 60 * 5,
  });
};

const useFeaturedVariantsBasedOnCategory = (categoryId) => {
  return useQuery({
    queryKey: ["variants", "featured", categoryId],
    queryFn: () => getFeaturedVariantsBasedOnCategory(categoryId),
    staleTime: 1000 * 60 * 5,
    enabled: !!categoryId,
  });
};

const useTopSellingProducts = () => {
  return useQuery({
    queryKey: ["products", "top selling"],
    queryFn: getAllTopSellingProducts,
    staleTime: 1000 * 60 * 5,
  });
};

const useNewArrivals = () => {
  return useQuery({
    queryKey: ["variants", "new arrivals"],
    queryFn: getAllNewArrivals,
    staleTime: 1000 * 60 * 5,
  });
};

const useRecommendation = (variantId) => {
  return useQuery({
    queryKey: ["recommendations", variantId],
    queryFn: () => getRecommendation(variantId),
    staleTime: 1000 * 60 * 5,
    enabled: !!variantId,
  });
};

const useVariantById = (variantId) => {
  return useQuery({
    queryKey: ["variant", variantId],
    queryFn: () => getVariant(variantId),
    staleTime: 1000 * 60 * 5,
    enabled: !!variantId,
  });
};

const useReviews = (productId) => {
  return useQuery({
    queryKey: ["reviews", productId],
    queryFn: () => getAllReviews(productId),
    staleTime: 1000 * 60 * 5,
    enabled: !!productId,
  });
};

const useReviewById = (productId, reviewId) => {
  return useQuery({
    queryKey: ["review", productId, reviewId],
    queryFn: () => getReviewById(productId, reviewId),
    staleTime: 1000 * 60 * 5,
    enabled: !!productId && !!reviewId,
  });
};

const useRatingDetails = (productId) => {
  return useQuery({
    queryKey: ["rating", productId],
    queryFn: () => getRatingDetails(productId),
    staleTime: 1000 * 60 * 5,
    enabled: !!productId,
  });
};

const useAddReview = (productId) => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: ({ reviewData, productId }) =>
      createReview(reviewData, productId),
    onSuccess: () => {
      queryClient.invalidateQueries(["reviews", productId]);
    },
  });
};

const useUpdateReview = (productId, reviewId) => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: ({ reviewData, productId, reviewId }) =>
      updateReview(reviewData, productId, reviewId),
    onSuccess: () => {
      queryClient.invalidateQueries(["review", productId, reviewId]);
    },
  });
};

const useDeleteReview = (productId, reviewId) => {
  const queryClient = useQueryClient();

  return useMutation({
    mutationFn: deleteReview,
    onSuccess: () => {
      queryClient.invalidateQueries(["review", productId, reviewId]);
    },
  });
};

export {
  useProductById,
  useVariants,
  useAllUtsavVariants,
  useAllFeaturedVariants,
  useFeaturedVariantsBasedOnCategory,
  useTopSellingProducts,
  useNewArrivals,
  useRecommendation,
  useVariantById,
  useReviews,
  useReviewById,
  useRatingDetails,
  useAddReview,
  useUpdateReview,
  useDeleteReview,
};
