import ImageMagnifier from "./ImageMagnifier";
import { useRef, useState } from "react";
import { IoChevronBack, IoChevronForward } from "react-icons/io5";
import useMediaQuery from "../hooks/useMediaQuery";
import Lightbox from "./Lightbox";
import { useModal } from "../contexts/modalContext";

const ProductGallery = ({ slides }) => {
  const minLg = useMediaQuery("(min-width: 1024px)");
  const minSm = useMediaQuery("(min-width: 640px)");
  const { openModal, closeModal } = useModal();
  const [currentSlide, setCurrentSlide] = useState(0);
  const productImages = slides.map((url) => ({ src: url }));
  const slideRef = useRef(null);
  const gap = minSm ? 16 : 8;
  const slidesToShow = 4;

  const calcX = (index) => {
    if (!slideRef.current) return 0;

    const slideWidth = slideRef.current.offsetWidth;

    if (index >= slidesToShow && index <= slides.length - slidesToShow) {
      return (index - slidesToShow + 1) * (slideWidth + gap);
    } else if (index > slides.length - slidesToShow) {
      return (slides.length - slidesToShow) * (slideWidth + gap);
    } else {
      return 0;
    }
  };

  const changeSlideTo = (index) => {
    if (index === currentSlide) return;
    setCurrentSlide(index);
  };

  const changeSlideForwards = () => {
    if (currentSlide >= slides.length - 1) return;
    setCurrentSlide((prevSlide) => prevSlide + 1);
  };

  const changeSlideBackwards = () => {
    if (currentSlide <= 0) return;
    setCurrentSlide((prevSlide) => prevSlide - 1);
  };

  const trackStyle = {
    transform: `translateX(-${calcX(currentSlide)}px)`,
  };

  return (
    <section className="w-[368px] max-xl:w-[304px] max-lg:w-[368px] max-sm:w-[312px] flex flex-col gap-4">
      <div className="IMAGE-CURRENT relative size-[368px] max-xl:size-[304px] max-lg:size-[368px] max-sm:size-[312px]">
        {slides.length > slidesToShow && minSm ? (
          <button
            onClick={changeSlideBackwards}
            className={`PREV absolute -left-10 top-1/2 -translate-y-1/2 ${
              currentSlide <= 0 ? "text-primary" : "text-black"
            }`}
          >
            <IoChevronBack size={32} />
          </button>
        ) : null}
        {minLg ? (
          <ImageMagnifier
            src={slides[currentSlide]}
            alt="product"
            magnifierWidth={600}
            magnifierHeight={400}
            zoomLevel={2.5}
          />
        ) : (
          <img
            src={slides[currentSlide]}
            alt="product"
            loading="lazy"
            onClick={() =>
              openModal(
                <Lightbox
                  handleClose={closeModal}
                  slides={productImages}
                  initialIndex={currentSlide}
                  zoom
                />
              )
            }
            className="size-full object-contain object-center cursor-pointer"
          />
        )}
        {slides.length > slidesToShow && minSm ? (
          <button
            onClick={() => changeSlideForwards()}
            className={`NEXT absolute -right-10 top-1/2 -translate-y-1/2 ${
              currentSlide >= slides.length - 1 ? "text-primary" : "text-black"
            }`}
          >
            <IoChevronForward size={32} />
          </button>
        ) : null}
      </div>

      <div className="ARRAY-CONTAINER">
        <div className="overflow-hidden">
          <div
            className="IMAGES-ARRAY flex gap-4 max-sm:gap-2 mx-auto w-max"
            style={trackStyle}
          >
            {slides.map((slide, index) => (
              <div
                ref={slideRef}
                key={index}
                className={`size-20 max-xl:size-16 max-lg:size-20 max-sm:size-18 border-2 flex-shrink-0 cursor-pointer ${
                  currentSlide === index
                    ? "border-primeRed"
                    : "border-transparent"
                }`}
                onMouseEnter={() => changeSlideTo(index)}
              >
                <img
                  src={slide}
                  alt={`product-${index}`}
                  loading="lazy"
                  className="size-full object-contain object-center"
                />
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

export default ProductGallery;
