import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";

const Variation = ({ product, variant, onPreview }) => {
  const navigate = useNavigate();
  const [selectedAttributes, setSelectedAttributes] = useState({});
  const [nonExistentCombinations, setNonExistentCombinations] = useState({});

  useEffect(() => {
    setSelectedAttributes(variant.attributes);
  }, [variant]);

  useEffect(() => {
    const newNonExistentCombinations = {};
    product.variationAttributes.forEach((attr) => {
      product.variation[attr].forEach((value) => {
        const newAttributes = { ...selectedAttributes, [attr]: value };
        const matchingVariant = product.variants.find((variant) =>
          Object.entries(newAttributes).every(
            ([key, val]) => variant.attributes[key] === val
          )
        );
        if (!matchingVariant || !matchingVariant.is_active) {
          if (!newNonExistentCombinations[attr]) {
            newNonExistentCombinations[attr] = [];
          }
          newNonExistentCombinations[attr].push(value);
        }
      });
    });
    setNonExistentCombinations(newNonExistentCombinations);
  }, [product, selectedAttributes]);

  const handleAttributeChange = (attr, value) => {
    const newAttributes = { ...selectedAttributes, [attr]: value };

    const matchingVariant = product.variants.find((variant) => {
      return Object.entries(newAttributes).every(
        ([key, val]) => variant.attributes[key] === val
      );
    });

    if (matchingVariant && matchingVariant.is_active) {
      setSelectedAttributes(newAttributes);
      navigate(`/product/${matchingVariant._id}`, { replace: true });
    } else {
      const closestVariant = getClosestValidCombination(attr, value);
      if (closestVariant) {
        setSelectedAttributes(closestVariant.attributes);
        navigate(`/product/${closestVariant._id}`, { replace: true });
      }
    }
  };

  const getClosestValidCombination = (attr, value) => {
    let updatedAttributes = { ...selectedAttributes, [attr]: value };

    let bestMatch = null;
    let bestMatchScore = -1;

    product.variants.forEach((variant) => {
      let score = 0;

      if (variant.attributes[attr] === value) {
        score += 2;
      }

      Object.entries(updatedAttributes).forEach(([key, val]) => {
        if (key !== attr && variant.attributes[key] === val) {
          score += 1;
        }
      });

      if (score > bestMatchScore && variant.is_active) {
        bestMatch = variant;
        bestMatchScore = score;
      }
    });

    return bestMatch || product.variants[0];
  };

  // const isFirstAttribute = (attr) =>
  //   product.variationAttributes.indexOf(attr) === 0;

  const isCSSColor = (color) => {
    const s = new Option().style;
    s.color = color;
    return s.color !== "";
  };

  const getColorStyle = (color) => {
    const matchingVariant = product.variants.find(
      (v) => v.attributes.color === color
    );
    const imageUrl = matchingVariant?.images[0];
    return isCSSColor(color)
      ? { backgroundColor: color }
      : {
          backgroundImage: `url(${imageUrl})`,
          backgroundSize: "cover",
          backgroundPosition: "center",
          backgroundRepeat: "no-repeat",
        };
  };

  const orderedVariationAttributes = [...product.variationAttributes];
  const colorIndex = orderedVariationAttributes.indexOf("color");

  if (colorIndex > -1) {
    orderedVariationAttributes.splice(colorIndex, 1);
    orderedVariationAttributes.unshift("color");
  }

  return (
    <div className="space-y-4">
      {orderedVariationAttributes.map((attr) => (
        <div key={attr} className="space-y-1">
          <div className="flex items-center gap-4">
            <h4 className="uppercase font-medium text-secondary text-sm">
              {attr}
            </h4>
            <div className="flex gap-1 items-center">
              {attr === "color" && (
                <span
                  className="size-4 rounded-full"
                  style={getColorStyle(selectedAttributes.color)}
                />
              )}
              <p className="text-sm font-medium capitalize">
                {variant.attributes[attr]}
              </p>
            </div>
          </div>

          <ul className="flex lg:flex-wrap items-center gap-x-4 gap-y-6 max-lg:overflow-auto no-scroll">
            {product.variation[attr].map((value) => {
              const isColorAttribute = attr === "color";
              const imageUrl = isColorAttribute
                ? product.variants.find((v) => v.attributes.color === value)
                    ?.images[0]
                : null;
              const isNonExistent =
                nonExistentCombinations[attr]?.includes(value);

              const variantWithAttributes = product.variants.find((variant) =>
                Object.entries({ ...selectedAttributes, [attr]: value }).every(
                  ([key, val]) => variant.attributes[key] === val
                )
              );
              // const isLowStock =
              //   isFirstAttribute(attr) &&
              //   variantWithAttributes &&
              //   variantWithAttributes.quantity < 5;

              return (
                <li
                  key={value}
                  onClick={() => handleAttributeChange(attr, value)}
                  className={`${
                    isColorAttribute
                      ? "size-18 p-1"
                      : "w-16 h-12 flex items-center justify-center"
                  }  ${
                    isNonExistent
                      ? isColorAttribute
                        ? "opacity-70 cursor-not-allowed"
                        : "bg-white border-secondary text-primary cursor-not-allowed"
                      : (selectedAttributes[attr] === value
                          ? isColorAttribute
                            ? "bg-white border-2 border-primeRed"
                            : "bg-primeRed border-primeRed text-white"
                          : isColorAttribute
                          ? "bg-white border-secondary"
                          : "bg-white border-black") + " cursor-pointer"
                  } border rounded text-sm relative flex-shrink-0 overflow-hidden`}
                >
                  {isColorAttribute && imageUrl ? (
                    <img
                      src={imageUrl}
                      alt={value}
                      loading="lazy"
                      onMouseEnter={() => onPreview(variantWithAttributes)}
                      onMouseLeave={() => onPreview(null)}
                      className="size-full object-contain object-center rounded-sm"
                    />
                  ) : (
                    value
                  )}

                  {/* {isLowStock && (
                    <span className="absolute top-full left-1/2 -translate-x-1/2 text-[8px] font-medium text-error whitespace-nowrap pt-[2px]">
                      Hurry! Only {variantWithAttributes.quantity} left.
                    </span>
                  )} */}
                </li>
              );
            })}
          </ul>
        </div>
      ))}
    </div>
  );
};

export default Variation;
