import { useParams } from "react-router-dom";
import Carousel from "../../components/Carousel";
import ProductCard from "../../components/ProductCard";
import useMediaQuery from "../../hooks/useMediaQuery";
import { useRecommendation } from "../../services/productService";

const Recommendations = () => {
  const { productId } = useParams();
  const { data: recommendations, isLoading } = useRecommendation(productId);
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const minMd = useMediaQuery("(min-width: 768px)");

  return !isLoading && recommendations?.length > 0 ? (
    <div className="container max-w-full space-y-8 max-lg:space-y-4">
      <h1 className="text-2xl max-lg:text-xl font-bold font-secondary">
        You may also like
      </h1>

      <Carousel
        slideWidth={!minMd ? 160 : 200}
        spaceBetween={maxXl ? 10 : 14}
        allowTouchMove
        navigation
      >
        {recommendations.map((product, index) => (
          <ProductCard key={index} product={product} />
        ))}
      </Carousel>
    </div>
  ) : null;
};

export default Recommendations;
