const ImagePreview = ({ images, limit = 5 }) => {
  const visibleImages = images.slice(0, limit);
  const remainingImagesCount = images.length - limit;

  return (
    <div className="flex space-x-3">
      {visibleImages.map((image, index) => (
        <div key={index} className="size-16 overflow-hidden rounded relative">
          <img
            src={typeof image === "string" ? image : URL.createObjectURL(image)}
            alt={`preview ${index + 1}`}
            className={`w-full h-full object-contain object-center ${
              index === limit - 1 && remainingImagesCount > 0
                ? "opacity-50"
                : ""
            }`}
          />
          {index === limit - 1 && remainingImagesCount > 0 && (
            <div className="absolute inset-0 flex items-center justify-center text-white bg-black bg-opacity-50">
              +{remainingImagesCount}
            </div>
          )}
        </div>
      ))}
    </div>
  );
};

export default ImagePreview;
