import { useRecentlyViewed } from "../contexts/RecentlyViewedContext";
import useMediaQuery from "../hooks/useMediaQuery";
import Carousel from "./Carousel";
import ProductCard from "./ProductCard";

const RecentlyViewed = () => {
  const { recentProducts } = useRecentlyViewed();
  const maxXl = useMediaQuery("(max-width: 1280px)");
  const minMd = useMediaQuery("(min-width: 768px)");

  return (
    recentProducts.length > 0 && (
      <div className="container max-w-full space-y-8 max-lg:space-y-4">
        <h1 className="text-2xl max-lg:text-xl font-bold font-secondary">
          Recently Viewed
        </h1>

        <Carousel
          slideWidth={!minMd ? 160 : 200}
          spaceBetween={maxXl ? 10 : 14}
          allowTouchMove
          navigation
        >
          {recentProducts.map((product, index) => (
            <ProductCard key={index} product={product} />
          ))}
        </Carousel>
      </div>
    )
  );
};

export default RecentlyViewed;
