import { useParams } from "react-router-dom";
import ProductGallery from "../../components/ProductGallery";
import Details from "./Details";
import useTitle from "../../hooks/useTitle";
import DeliveryCheck from "./DeliveryCheck";
import Info from "./Info";
import Review from "./Review";
import { useEffect, useState } from "react";
import productBg from "../../assets/images/utsav-product-bg.png";
import { useWishlist } from "../../contexts/wishlistContext";
import { useRecentlyViewed } from "../../contexts/RecentlyViewedContext";
import { IoMdHeart } from "react-icons/io";
import { useVariantById } from "../../services/productService";
import Recommendations from "./Recommendations";
import RecentlyViewed from "../../components/RecentlyViewed";
import Loader from "../../components/Loader";

const Product = () => {
  const { productId } = useParams();
  const { data: product, isLoading, error } = useVariantById(productId);
  const [previewProduct, setPreviewProduct] = useState(null);
  const { addToWishlist, removeFromWishlist, isWished } = useWishlist();
  const { addProduct } = useRecentlyViewed();
  useTitle(`Buy ${product?.name}`);

  const toggleWishlist = () => {
    if (isWished(product._id)) {
      removeFromWishlist(product._id);
    } else {
      addToWishlist(product._id);
    }
  };

  const handlePreview = (id) => {
    setPreviewProduct(id);
  };

  useEffect(() => {
    if (product) {
      addProduct(product);
    }
    // eslint-disable-next-line
  }, [product]);

  if (
    isLoading ||
    (!error && !product) ||
    (!error && !product?.productGroup._id)
  ) {
    return <Loader />;
  }

  if (error) {
    return (
      <div className="w-full">
        <h2 className="text-center font-secondary font-medium text-xl mt-8">
          {error.message}
        </h2>
      </div>
    );
  }

  return (
    <div
      className="py-8 md:space-y-12 space-y-8"
      style={
        product.isUtsav
          ? {
              
            }
          : {}
      }
    >
      <section className="PRODUCT-HEAD container max-w-full px-16 max-xl:px-12 max-lg:px-8 max-md:px-4 flex max-lg:flex-col max-lg:items-center lg:gap-40 gap-4">
        <div className="RIGHT-SECTION size-max sticky max-lg:relative lg:top-28 z-[5]">
          <ProductGallery
            slides={
              previewProduct
                ? previewProduct?.images
                : [
                    ...product?.images,
                    ...product?.productGroup?.otherImages,
                    ...product?.productGroup?.thumbnail,
                  ]
            }
            product={product}
          />
          <button
            onClick={toggleWishlist}
            className="WISHLIST-BTN absolute right-4 top-4 size-8 rounded-full flex justify-center items-center"
          >
            <IoMdHeart
              className={`${
                isWished(product._id)
                  ? "text-primeRed"
                  : "text-halfGray stroke-2 stroke-black"
              }`}
              size={26}
            />
          </button>
        </div>

        <div className="LEFT-SECTION w-full">
          <Details product={product} handlePreview={handlePreview} />
          <DeliveryCheck />
          <Info product={product} />
          <Review productId={product.productGroup?._id} />
        </div>
      </section>

      <Recommendations />
      <RecentlyViewed />
    </div>
  );
};

export default Product;
