import { MdVerifiedUser } from "react-icons/md";
import Rating from "../../components/Rating";
import ImagePreview from "../../components/ImagePreview";
import { BiDislike, BiLike } from "react-icons/bi";
import Button from "../../components/Button";
import { formatDate } from "../../utils/dateUtils";
import { useMemo } from "react";

const Review = ({ review }) => {
  return (
    <li className="space-y-2 py-4">
      <div className="HEADER flex items-center justify-between">
        <div className="CUSTOMER-DETAILS flex items-center gap-2">
          <p className="text-sm">{review.userId?.fullName}</p>

          {review.userId?.email_validation && (
            <div className="flex items-center gap-[2px] text-xxs">
              <span>
                <MdVerifiedUser className="text-success" />
              </span>
              <span className="pt-[2px] text-primary">Verified Buyer</span>
            </div>
          )}
        </div>

        <p className="text-sm text-secondary">
          {formatDate(new Date(review.createdAt))}
        </p>
      </div>

      <Rating rating={review.rating} size={18} />

      <h3 className="TITLE font-medium">{review.title}</h3>

      <p className="text-sm text-dark pb-2">{review.comment}</p>

      {review.imgLink?.length > 0 && <ImagePreview images={review.imgLink} />}

      <div className="LIKE-DISLIKE flex items-center justify-end gap-4">
        <p className="text-sm">Was this helpful?</p>

        <div className="LIKE flex items-center gap-1">
          <button>
            <BiLike />
          </button>
          <span>0</span>
        </div>

        <div className="DISLIKE flex items-center gap-1">
          <button>
            <BiDislike />
          </button>
          <span>0</span>
        </div>
      </div>
    </li>
  );
};

const ReviewList = ({ reviews }) => {
  const limit = 20;

  const currentReviews = useMemo(() => {
    return reviews.slice(0, limit);
  }, [reviews, limit]);

  return (
    <section className="border-t-2 border-primary py-4">
      <h2 className="text-lg font-medium font-secondary">
        Reviews from customers
      </h2>

      <ul className="divide-y divide-secondary">
        {currentReviews.map((review, index) => (
          <Review key={index} review={review} />
        ))}
      </ul>

      {reviews.length > limit && (
        <Button type="button" reverse>
          <p className="text-sm">view all reviews</p>
        </Button>
      )}
    </section>
  );
};

export default ReviewList;
