import Rating from "./Rating";
import { IoMdStar } from "react-icons/io";

const getColorForRating = (rating) => {
  switch (rating) {
    case "5":
      return "bg-green-500";
    case "4":
      return "bg-green-400";
    case "3":
      return "bg-yellow-400";
    case "2":
      return "bg-orange-400";
    case "1":
      return "bg-red-500";
    default:
      return "bg-gray-200";
  }
};

const RatingDetails = ({
  rating,
  maxRating = 5,
  totalRatings,
  ratingsBreakdown,
}) => {
  return (
    <div className="flex items-center space-x-16 p-4">
      <div className="text-center">
        <div className="text-4xl font-bold">{rating.toFixed(1)}</div>
        <div className="text-secondary">{totalRatings} Ratings</div>
        <div className="mt-2">
          <Rating rating={rating} size={24} />
        </div>
      </div>
      {ratingsBreakdown && (
        <div className="w-full space-y-2">
          {Object.keys(ratingsBreakdown)
            .reverse()
            .map((rating, index) => (
              <div key={index} className="flex items-center gap-2">
                <span className="text-secondary">{rating}</span>
                <IoMdStar size={18} className="text-secondary mr-2" />
                <div className="flex-1 h-2 bg-lightGray rounded-full overflow-hidden">
                  <div
                    className={`h-full ${getColorForRating(rating)}`}
                    style={{
                      width: `${
                        (ratingsBreakdown[rating] / totalRatings) * 100
                      }%`,
                    }}
                  />
                </div>
                <span className="ml-2 text-secondary">
                  ({ratingsBreakdown[rating]})
                </span>
              </div>
            ))}
        </div>
      )}
    </div>
  );
};

export default RatingDetails;
