import { useState } from "react";

const ImageMagnifier = ({
  src,
  alt,
  magnifierHeight = 500,
  magnifierWidth = 500,
  zoomLevel = 2.5,
}) => {
  const [[x, y], setXY] = useState([0, 0]);
  const [[imgWidth, imgHeight], setSize] = useState([0, 0]);
  const [showMagnifier, setShowMagnifier] = useState(false);

  return (
    <div className="relative size-full">
      <img
        src={src}
        alt={alt}
        loading="lazy"
        className="size-full object-contain object-center"
        onMouseEnter={(e) => {
          // Update image size and turn on magnifier
          const elem = e.currentTarget;
          const { width, height } = elem.getBoundingClientRect();
          setSize([width, height]);
          setShowMagnifier(true);
        }}
        onMouseMove={(e) => {
          // Update cursor position
          const elem = e.currentTarget;
          const { top, left } = elem.getBoundingClientRect();

          // Calculate cursor position on the image
          const x = e.pageX - left - window.pageXOffset;
          const y = e.pageY - top - window.pageYOffset;

          // Clamp the x, y values to stay within the image boundaries
          const clampedX = Math.max(
            magnifierWidth / (2 * zoomLevel),
            Math.min(x, imgWidth - magnifierWidth / (2 * zoomLevel))
          );
          const clampedY = Math.max(
            magnifierHeight / (2 * zoomLevel),
            Math.min(y, imgHeight - magnifierHeight / (2 * zoomLevel))
          );

          setXY([clampedX, clampedY]);
        }}
        onMouseLeave={() => {
          // Close magnifier
          setShowMagnifier(false);
        }}
      />

      {showMagnifier && (
        <>
          {/* Magnifier */}
          <div
            style={{
              position: "fixed",
              top: "50%",
              transform: "translateY(-50%)",
              pointerEvents: "none",
              height: `${magnifierHeight}px`,
              width: `${magnifierWidth}px`,
              opacity: "1",
              border: "1px solid lightgray",
              backgroundColor: "white",
              backgroundImage: `url('${src}')`,
              backgroundRepeat: "no-repeat",
              backgroundSize: `${imgWidth * zoomLevel}px ${
                imgHeight * zoomLevel
              }px`,
              backgroundPositionX: `${-x * zoomLevel + magnifierWidth / 2}px`,
              backgroundPositionY: `${-y * zoomLevel + magnifierHeight / 2}px`,
            }}
            className="shadow-pop left-2/4 max-xl:left-[40%]"
          />
          {/* Mark on the Image */}
          <div
            style={{
              position: "absolute",
              top: `${y - magnifierHeight / (2 * zoomLevel)}px`,
              left: `${x - magnifierWidth / (2 * zoomLevel)}px`,
              width: `${magnifierWidth / zoomLevel}px`,
              height: `${magnifierHeight / zoomLevel}px`,
              border: "1px solid red",
              pointerEvents: "none",
              boxSizing: "border-box",
            }}
          />
        </>
      )}
    </div>
  );
};

export default ImageMagnifier;
