import axiosInstance from "./axiosInstance";

const getProductById = async (productId) => {
  const response = await axiosInstance.get(`/getProductById/${productId}`);
  return response.data;
};

const getAllVariants = async (params) => {
  const response = await axiosInstance.get("/getAllVariantsOnUser", { params });
  return response.data;
};

const getAllUtsavVariants = async () => {
  const response = await axiosInstance.get("/getAllUtsavProduct");
  return response.data?.productDetails;
};

const getAllFeaturedVariants = async () => {
  const response = await axiosInstance.get("/getAllFeaturedProduct");
  return response.data?.variantDetails;
};

const getFeaturedVariantsBasedOnCategory = async (categoryId) => {
  const response = await axiosInstance.get(
    `/getFeaturedProductBasedOnCategory/${categoryId}`
  );
  return response.data?.productList;
};

const getAllNewArrivals = async () => {
  const response = await axiosInstance.get("/getAllNewArrivals");
  return response.data?.productDetails;
};

const getAllTopSellingProducts = async () => {
  const response = await axiosInstance.get("/getAllTopSellingProduct");
  return response.data;
};

const getVariant = async (variantId) => {
  const response = await axiosInstance.get(`/getVariantById/${variantId}`);
  return response.data?.variantDetails;
};

const getRecommendation = async (variantId) => {
  const response = await axiosInstance.get(
    `/suggestionProductList/${variantId}`
  );
  return response.data?.suggestionProductList;
};

const getAllReviews = async (productId) => {
  const response = await axiosInstance.get(`/GetAllReviews/${productId}`);
  return response.data;
};

const getReviewById = async (productId, reviewId) => {
  const response = await axiosInstance.get(
    `/reviewByID/${productId}/${reviewId}`
  );
  return response.data;
};

const getRatingDetails = async (productId) => {
  const response = await axiosInstance.get(`/getAvgRating/${productId}`);
  return response.data;
};

const createReview = async (reviewData, productId) => {
  const response = await axiosInstance.post(
    `/createReview/${productId}`,
    reviewData,
    { headers: { "Content-Type": "multipart/form-data" } }
  );
  return response.data;
};

const updateReview = async (reviewData, productId, reviewId) => {
  const response = await axiosInstance.post(
    `/updateReview/${productId}/${reviewId}`,
    reviewData
  );
  return response.data;
};

const deleteReview = async (reviewId) => {
  const response = await axiosInstance.get(`/deleteReview/${reviewId}`);
  return response.data;
};

export {
  getProductById,
  getAllVariants,
  getAllUtsavVariants,
  getAllFeaturedVariants,
  getFeaturedVariantsBasedOnCategory,
  getAllNewArrivals,
  getAllTopSellingProducts,
  getVariant,
  getRecommendation,
  createReview,
  getAllReviews,
  getReviewById,
  getRatingDetails,
  updateReview,
  deleteReview,
};
