import { useState } from "react";
import { IoMdStar } from "react-icons/io";
import Rating from "./Rating";

const getColorForRating = (rating) => {
  switch (true) {
    case rating >= 4:
      return "bg-green-500";
    case rating >= 3:
      return "bg-green-400";
    case rating >= 2:
      return "bg-yellow-400";
    case rating >= 1:
      return "bg-orange-400";
    default:
      return "bg-gray-200";
  }
};

const getColorForRatingDetails = (rating) => {
  switch (rating) {
    case 5:
      return "bg-green-500";
    case 4:
      return "bg-green-400";
    case 3:
      return "bg-yellow-400";
    case 2:
      return "bg-orange-400";
    case 1:
      return "bg-red-500";
    default:
      return "bg-gray-200";
  }
};

const RatingDetailsDropdown = ({ rating, totalRatings, ratingsBreakdown }) => {
  return (
    <div className="w-64 p-4 bg-white border rounded shadow-lg">
      <div className="text-center">
        <div className="text-4xl font-bold">{rating.toFixed(1)}</div>
        <div className="text-secondary">{totalRatings} Ratings</div>
        <div className="mt-2 flex justify-center">
          <Rating rating={rating} size={24} />
        </div>
      </div>
      {ratingsBreakdown && (
        <div className="mt-4 space-y-1">
          {Object.entries(ratingsBreakdown)
            .sort(([a], [b]) => b - a)
            .map(([key, count]) => (
              <div key={key} className="flex items-center gap-1">
                <span className="text-secondary">{key}</span>
                <IoMdStar size={18} className="text-secondary mr-2" />
                <div className="flex-1 h-2 bg-lightGray rounded-full overflow-hidden">
                  <div
                    className={`h-full ${getColorForRatingDetails(
                      Number(key)
                    )}`}
                    style={{ width: `${(count / totalRatings) * 100}%` }}
                  />
                </div>
                <span className="ml-2 text-secondary">({count})</span>
              </div>
            ))}
        </div>
      )}
    </div>
  );
};

const RatingBadge = ({
  rating,
  totalRatings,
  ratingsBreakdown,
  dropdown = false,
}) => {
  const [showDropdown, setShowDropdown] = useState(false);

  return (
    <div
      className="relative inline-block"
      onMouseEnter={() => setShowDropdown(true)}
      onMouseLeave={() => setShowDropdown(false)}
    >
      <div
        className={`flex items-center justify-center gap-[2px] px-[5px] py-[2px] rounded text-white ${getColorForRating(
          rating
        )}`}
      >
        <span className="text-xs font-medium pt-[2px] leading-none">
          {rating.toFixed(1)}
        </span>
        <IoMdStar size={12} />
      </div>
      {dropdown && showDropdown && (
        <div className="absolute z-10 mt-2">
          <RatingDetailsDropdown
            rating={rating}
            totalRatings={totalRatings}
            ratingsBreakdown={ratingsBreakdown}
          />
        </div>
      )}
    </div>
  );
};

export default RatingBadge;
