import { useEffect, useState } from "react";
import { FiMinus, FiPlus } from "react-icons/fi";

const QuantityInput = ({
  value = 1,
  min = 1,
  max = Infinity,
  onIncrease = () => {},
  onDecrease = () => {},
  readOnly = false,
  btnClass = "w-6",
  containerClassName = "h-5 rounded",
}) => {
  const [quantity, setQuantity] = useState(value);

  useEffect(() => {
    setQuantity(value);
  }, [value]);

  const handleIncrease = () => {
    if (quantity < max) {
      const newQuantity = quantity + 1;
      setQuantity(newQuantity);
      onIncrease(newQuantity);
    }
  };

  const handleDecrease = () => {
    if (quantity > min) {
      const newQuantity = quantity - 1;
      setQuantity(newQuantity);
      onDecrease(newQuantity);
    }
  };

  return (
    <div
      className={`w-max flex items-center border overflow-hidden ${containerClassName}`}
    >
      <button
        type="button"
        onClick={handleDecrease}
        className={`bg-offWhite hover:bg-mainGray border-r h-full flex items-center justify-center ${btnClass}`}
        disabled={quantity <= min}
      >
        <FiMinus
          size={14}
          className={quantity <= min ? "text-primary" : "text-dark"}
        />
      </button>
      <input
        type="number"
        value={quantity}
        min={min}
        readOnly={readOnly}
        onChange={(e) => setQuantity(e.target.value)}
        className="text-dark bg-offWhite outline-none w-8 h-full text-center text-sm [appearance:textfield] [&::-webkit-outer-spin-button]:appearance-none [&::-webkit-inner-spin-button]:appearance-none read-only:cursor-default"
      />
      <button
        type="button"
        onClick={handleIncrease}
        className={`bg-offWhite hover:bg-mainGray border-l h-full flex items-center justify-center ${btnClass}`}
        disabled={quantity >= max}
      >
        <FiPlus
          size={14}
          className={quantity >= max ? "text-primary" : "text-dark"}
        />
      </button>
    </div>
  );
};

export default QuantityInput;
